*,
*::before,
*::after {
    margin: 0;
    padding: 0;
}

body {
    font-family: $main-font;
    color: $primary-text-color;
    background-color: $primary-white-color;
}
body.modal-open {
    overflow: hidden;
}

.list {
    list-style: none;
}

.link {
    text-decoration: none;
}

.address {
    font-style: normal;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

a:focus {
    outline: none;
}

button {
    cursor: pointer;
}
.accent {
    color: $accent-color;
}