.methods {
  padding-top: 50px;
  padding-bottom: 50px;
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  &__item {
    align-items: center;
    justify-content: center;

    width: 280px;
    height: 280px;

    background: #ffffff;
    box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
  }
  &__item:hover {
    background-color: $accent-color;
  }
  &__title {
    font-weight: 800;
    font-size: 20px;
    line-height: 1.3;
    /* or 26px */

    text-align: center;
    letter-spacing: 0.03em;
    color: #111518;

    margin-bottom: 20px;
  }
  &__svg {
    fill: $accent-color;
    display: block;
    margin-right: auto;
    margin-left: auto;
    margin-top: 30px;
    margin-bottom: 20px;
  }

  &__therm {
    display: flex;
    flex-direction: column;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.43;
    /* or 143% */

    text-align: center;
    letter-spacing: 0.08em;

    color: #111518;
    align-items: center;
    margin-bottom: 16px;
  }
  &__therm::after {
    margin-top: 16px;
    display: inline-block;
    content: '';
    width: 21px;
    height: 2px;
    background-color: $accent-color;
  }
  &__text {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;
    /* or 167% */

    text-align: center;
    letter-spacing: 0.08em;

    color: #b9b9b9;
  }
  &__item:hover .methods__svg {
    fill: #ffffff;
  }
  &__item:hover .methods__therm {
    color: #ffffff;
  }
  &__item:hover .methods__text {
    color: #ffffff;
  }
  &__item:hover .methods__therm::after {
    background-color: #ffffff;
  }
}

@media screen and (min-width: 480px) {
  .methods {
    width: 480px;
    &__item {
      margin-bottom: 15px;
    }
  }
}
@media screen and (min-width: 768px) {
  .methods {
    &__title {
      font-size: 26px;
      line-height: 1.3;
    }
    &__item {
      margin-bottom: 0;
      width: 334px;
      height: 334px;
    }
    &__svg {
      margin-top: 40px;
      margin-bottom: 30px;
    }
    &__therm {
      margin-bottom: 26px;
    }
    &__therm::after {
      margin-top: 26px;
    }
    &__item:not(:nth-child(even)) {
      margin-right: 20px;
    }
    &__item + &__item + &__item {
      margin-top: 27px;
    }
  }
}

@media screen and (min-width: 1344px) {
  .methods {
    width: 1344px;
    &__title {
      text-align: start;
      font-size: 46px;
      line-height: 1.3;
    }
    &__item {
      margin-bottom: 0;
      width: 424px;
      height: 396px;
    }
    &__therm {
      margin-bottom: 16px;
      font-size: 20px;
      line-height: 1.4;
    }
    &__therm::after {
      margin-top: 16px;
    }
    &__text {
      font-size: 18px;
      line-height: 1.56;
    }
    &__svg {
      margin-top: 40px;
      margin-bottom: 30px;
    }
  }
}
